import { BuildAndExportEngine, UniformServerConfig } from '@uniformdev/common-server';
import { Logger } from '@uniformdev/common';
import { buildAndExport } from './buildAndExport';

export class NextBuildAndExportEngine implements BuildAndExportEngine {
    async execute(outputDir: string, config: UniformServerConfig, logger: Logger): Promise<void> {
        await buildAndExport(outputDir, config, logger);
    }
}
